@import url('https://fonts.googleapis.com/css?family=Open+Sans');

html,body {
  font-family: "Open Sans", serif;
  background-color: black;
}
.container{
  max-width: 100%;
 
}
.wrapper {
  
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  width:90%;
  height: 100vh;
  box-shadow: 0px 2px 80px -20px rgba(255, 255, 255, 0.438);
  margin:0 auto;

}

.info {

  height: 100vh;
  background: url("img/weather.jpg") center center no-repeat;
  background-blend-mode: multiply;
  background-color: #8b8b8b;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.form {
  
  height: 100vh;
  padding: 5px;
  background-color: #103a6554;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 2px #113a65;
  width: 50%;
  padding-bottom: 4px;
  color: #fff;
  font-weight: lighter;
  font-size: 2em;
  outline: none;
  width: 100%;
  margin: 10px 0;
}

input::-webkit-input-placeholder {
  color: #e0e0e0;
}

input::-moz-placeholder {
  color: #e0e0e0;
}

button {
  padding: 8px 20px;
  margin: 5px 0 ;
  cursor: pointer;
  background-color: #f3ec8a;
  color: rgb(39, 40, 94);
  border: none;
  width: 100%;
  text-transform: uppercase;
  font-size: 1.3em;
}

button:hover {
  position: relative;
  top: 2px;
  background-color: rgb(182, 235, 130);
}

button:active {
 
  border-color: transparent;
  position: relative;
  top: 2px;
  background-color: rgb(182, 235, 130);
}

.info h2 {
  font-size: 2em;
  color: #fff;
}

.info p {
  font-size: 1.3em;
  color: #e4e4e4;

}

.infoWeath {
  padding: 0;
  font-size: 1.3em;
  color: #e0e0e0;
  background-color: #103a659f;
  padding: 5px;
}

.error {
  color: #fdee69;
}
@media all and (orientation:landscape) {
 
  .main,.info, .form{
    height:100vh;
    font-size: 14px;
    }
    .infoWeath{
      
      font-size: 2em;
    }
}